import React, {useState} from 'react';
import {NavLink} from "react-router-dom";
import Axios from "axios";

function LoginForm(props) {

    const [data, setData] = useState({
        email:'',
        password: ""
    })

    const handleDeleteClick = () => {
        Axios.delete("/api/delete-cookie")
            .then(res => {
                alert(" ")
            })
    }

    const handleLoginClick = () => {
        // alert(data.id);
        // alert(data.password);
        //dto /user/login  
        let url = "/user/login";
        Axios.post(url, data).then(res => {
            console.log(res);
        }).catch(error => {
            //   
            alert(" .");
        });
    }

    const handleIsLoginClick = () => {
        let url = "/user/islogin";
        Axios.get(url).then(res => {
            alert(" ")
        }).catch(error => {
            //   
            alert(" ");
        });
    }

    return (
        <div>
            <NavLink to={"/"}><h2>Menu</h2></NavLink>
            <h1>LoginForm</h1>
            <h3 onClick={handleDeleteClick}>  </h3>
            <br/><br/>
            <div>
                <input type="text" value={data.email} onChange={(e) => setData({
                    ...data,
                    email: e.target.value
                })
                }/>
            </div>
            <div>
                <input type="password" value={data.password} onChange={(e) => setData({
                    ...data,
                    password: e.target.value
                })
                }/>
                <button onClick={handleLoginClick}></button>
            </div>
            <br/>
            <button onClick={handleIsLoginClick}>islogin</button>
        </div>
    );
}

export default LoginForm;