import React from 'react';
import Axios from "axios";

function Unauthorized(props) {
    const handleUnauthClick = () => {
        const url = "/user/rejection";

        Axios.get(url)
            .then(res => {
                alert("");
            })
            .catch(error => {
                //  
                console.error(error);
            });
    }

    return (
        <div>

            <h3 onClick={handleUnauthClick}>Spring rejection </h3>
        </div>
    );
}

export default Unauthorized;